import React from 'react';
import { Card, CardContent, Typography, Grid } from '@material-ui/core';
import CountUp from 'react-countup';
import cx from 'classnames';
import Divider from '@material-ui/core/Divider'

import styles from './Cards.module.css';

const Info = ({ data: { confirmed, recovered, deaths, lastUpdate } }) => {
  if (!confirmed) {
    return 'Loading...';
  }

  return (
    <div className={styles.container}>
      <Grid container spacing={3} justify="center">
        <Grid item xs={12} md={3} component={Card} className={cx(styles.card, styles.infected)} style={{backgroundColor:"#F4F4F4"}}>
          <Typography variant="h4" gutterBottom style={{color: "blue"}}> Confirmed </Typography>
          <Divider />
          <CardContent>
            <Typography variant="h5" component="h2">
              <CountUp start={0} end={confirmed.value} duration={2.75} separator="," />
            </Typography>
          </CardContent>
        </Grid>
        <Grid item xs={12} md={3} component={Card} className={cx(styles.card, styles.recovered)} style={{backgroundColor:"#F4F4F4"}}>
        <Typography variant="h4" gutterBottom style={{color: "green"}}> Recovered </Typography>
          <Divider />
          <CardContent>
            <Typography variant="h5" component="h2">
              <CountUp start={0} end={recovered.value} duration={2.75} separator="," />
            </Typography>
          </CardContent>
        </Grid>
        <Grid item xs={12} md={3} component={Card} className={cx(styles.card, styles.deaths)} style={{backgroundColor:"#F4F4F4"}}>
        <Typography variant="h4" gutterBottom style={{color: "red"}}> Deaths </Typography>
          <Divider />
          <CardContent>
            <Typography variant="h5" component="h2">
              <CountUp start={0} end={deaths.value} duration={2.75} separator="," />
            </Typography>
          </CardContent>
        </Grid>
      </Grid>
    </div>
  );
};
export default Info;
